.footer_form {
	margin-left: 45px;
	margin-right: 45px;
	padding-left: 75px;
	padding-right: 75px;
	padding-top: 50px;
	padding-bottom: 165px;
	transform: translateY(-50%);
	display: flex;
	flex-wrap: wrap;
	position: relative;
	._title {
		width: 100%; }
	.text {
		width: 26.63%;
