.form_grid {
	display: flex;
	flex-wrap: wrap;
	margin-left: -5px;
	margin-right: -5px;
	> * {
		flex-shrink: 0; }
	.form_box {
		padding-left: 5px;
		padding-right: 5px;
		width: 100%;
		@media (min-width: 576px) {
			&.sm_50 {
				width: 50%; } }
		@media (min-width: 768px) {
			&.md_100 {
				width: 100%; }
			&.md_50 {
				width: 50%; }
			&.md_55 {
				width: 55%; }
			&.md_45 {
				width: 45%; } } } }

