@mixin i2p($color) {
	font-size: 14.94px;
	font-weight: normal;
	font-family: "Optima";
	color: $color;
	line-height: 2.2; }

@keyframes scollDown {
	from {
		transform: translateY(-100%); }
	95% {
		transform: translateY(100%); }
	100% {
		transform: translateY(100%); } }
.scroll-down-box {
	pointer-events: none;
	position: absolute;
	top: 100vh;
	transform: translateY(-100%);
	right: 100px;
	z-index: 10;
	display: flex;
	flex-direction: column;
	align-items: center;
	transition: all .3s ease-in-out;
	&.hide {
		opacity: 0;
		transform: translateY(-90%); }
	.bar {
		height: 200px;
		width: 1px;
		// background: #c1b081
		position: relative;
		overflow: hidden;
		&:before {
			animation-name: scollDown;
			animation-duration: 3s;
			animation-iteration-count: infinite;
			position: absolute;
			content: '';
			width: 1px;
			height: 200px;
			top: 0;
			transform: translateY(-100%);
			left: 0;
			background: #c1b081;
			background: -moz-linear-gradient(90deg, rgba(193,176,129,1) 0%, rgba(193,176,129,0) 100%);
			background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, rgba(193,176,129,0)), color-stop(100%, rgba(193,176,129,1)));
			background: -webkit-linear-gradient(90deg, rgba(193,176,129,1) 0%, rgba(193,176,129,0) 100%);
			background: -o-linear-gradient(90deg, rgba(193,176,129,1) 0%, rgba(193,176,129,0) 100%);
			background: -ms-linear-gradient(90deg, rgba(193,176,129,1) 0%, rgba(193,176,129,0) 100%);
			background: linear-gradient(0deg, rgba(193,176,129,1) 0%, rgba(193,176,129,0) 100%);
			filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#c1b081', endColorstr='#c1b081',GradientType=0 ); } }
	.text {
		font-family: "Lato";
		font-weight: 300;
		font-size: 18px;
		color: #e9e7da;
		text-transform: uppercase;
		margin-top: 20px; } }

.i2_main_banner {
	.block-1 {
		text-align: center;
		max-width: 859px;
		margin: 0 auto;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		min-height: 100vh;
		.bar {
			margin-top: 50px;
			width: 40px;
			height: 2px;
			background: #e0d4ac;
			margin-left: auto;
			margin-right: auto;
			margin-bottom: 15px; }
		h4 {
			font-family: "Lato";
			font-weight: normal;
			font-size: 29px;
			color: white;
			text-transform: uppercase; }
		h3 {
			font-family: "Lato";
			font-weight: 900;
			font-size: 59px;
			color: #dfcc8b;
			text-transform: uppercase;
			letter-spacing: 4px;
			margin-bottom: 36px; }
		p {
			font-family: "Optima";
			font-size: 16px;
			color: #f2f2f2;
			line-height: 2.5;
			margin-bottom: 0; } }

	.block-2 {
		display: flex;
		margin-top: 95px;
		.img {
			width: 40%; }
		.text {
			padding-top: 90px;
			width: 60%;
			padding-right: 68px;
			padding-left: 50px;
			h3 {
				font-size: 45px;
				color: #f8f8f8;
				font-family: "Lato";
				font-weight: 900;
				text-transform: uppercase;
				letter-spacing: 7px;
				margin-bottom: 37px;
				position: relative;
				span {
					position: relative; }
				&:before {
					width: 165px;
					height: 1px;
					background: #ded2ab;
					position: absolute;
					content: '';
					top: 50%;
					left: 0;
					transform: translateX(-50%); } }
			p {
				font-family: "Optima";
				font-size: 16px;
				color: white;
				line-height: 2;
				margin-bottom: 22px; } } } }


